.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgb(37, 209, 14);
  display: inline-block;
}

.node-finish {
  background-color: red;
}

.node-start {
  background-color: rgb(0, 217, 255);
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(55, 97, 17, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(28, 161, 16, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(42, 129, 64, 0.89);
  }

  100% {
    transform: scale(1);
    background-color: rgb(252, 255, 80);
  }
}

.node-wall {
  background-color: rgb(255, 255, 255);
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: .5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(16, 85, 7);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(79, 209, 62);
  }

  100% {
    transform: scale(1);
    background-color: rgb(51, 255, 0);
  }
}

body {
  background-color: black;
}